import { Button, List, ListItem, Popover } from "@mui/material"
import { useState } from "react"
import { Link } from "react-scroll"
import logo from "../assets/images/logo.png"

export default function Header() {

    const [popMenu, setPopMenu] = useState<any>(null)

    return (
        <header>
            <div className="nav-start">
                <img src={logo} alt="logo" width={96} />
            </div>
            <div className="nav-center">
                <Button >
                    <Link to="product" spy={true} smooth={true}>
                        Produk
                    </Link>
                </Button>
                <Button >
                    <Link to="story" spy={true} smooth={true}>
                        Cerita
                    </Link>
                </Button>
                <Button>
                    <Link to="contact" spy={true} smooth={true}>
                        Kontak
                    </Link>
                </Button>
            </div>
            <div className="nav-end">
                <Button onClick={(e) => setPopMenu(e.currentTarget)}>
                    Akun
                </Button>
                <Popover
                    open={Boolean(popMenu)}
                    anchorEl={popMenu}
                    onClose={() => setPopMenu(null)}
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'left',
                    }}
                >
                    <List>
                        <ListItem button>
                            Pesanan
                        </ListItem>
                        <ListItem button>
                            Perbaikan
                        </ListItem>
                        <ListItem button>
                            Pengaturan Akun
                        </ListItem>
                    </List>
                </Popover>
            </div>
        </header>
    )
}